.home {
  width: 100%;
  align-items: center;
  font-family: "Arial", sans-serif;
  color: #4e4e94;
}

.about {
  background: rgba( 255, 255, 255, 0.3 );
  backdrop-filter: blur(7.5px);
  margin: 70px;
  z-index: 4;
  max-width: 100%;
  height: calc(100vh - 100px);
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
  /* background-color: #4e4e94; */
  color: #ccccff;
  border-radius: 20px; 
}

.about h2 {
  font-family: "Segoe UI", Tahoma, Geneva, Verdana, sans-serif;
  font-size: 80px;
  /* color: #ccccff; */
  height: 50px;
}

.about .prompt {
  width: 40%;
  font-size: 30px;
  color: #4e4e94;
}
.prompt svg {
  font-size: 60px;
  margin: 5px;
  /* color: white; */
}

.skills {
  font-size: 35px;
  width: 100%;
  display: flex;
  justify-content: center;
  flex-direction: column;
  align-items: center;
}

.skills h1 {
  color: #4e4e94;
}

.skills .list {
  list-style: none;
  width: 60%;
}

.list span {
  font-size: 20px;
}

@media only screen and (max-width: 600px) {
  .about h2 {
    font-size: 40px;
  }

  .about .prompt {
    margin-top: 10px;
    font-size: 20px;
    width: 80%;
  }

  .skills {
    text-align: center;
  }
  .list {
    padding: 0;
  }

  .skills h2 {
    font-size: 30px;
  }
}
